<template>
	<view class="box">
		<view class="boxbg">
			<view class="service-number">
				<view class="service">一键劳作服务</view>
				<view class="order-number">订单编号：{{orderNo}}</view>
			</view>
			<textarea class="textarea" v-model="evaContent" maxlength="200" placeholder="输入评价内容"></textarea>
			<view class="btn-box">
				<view class="btn" @click="submit">提交评价</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		PostEvaOrderPackageAjax,
		PostEvaOrderHarvestAjax
	} from '@/apis/api/modules/user-center'
	export default {
		data() {
			return {
				orderNo: '',
				evaContent: '',
				orderId: '',
				type: ''
			}
		},
		onLoad(e) {
			this.orderNo = e.orderNo
			this.orderId = e.orderId
			this.type = e.type
		},
		methods: {
			async submit() {
				console.log('this.type', typeof this.type)
				if (!this.evaContent) {
					uni.showToast({
						title: '请输入评价！',
						icon: 'error'
					})
					return false
				}
				if (Number(this.type) === 0) {
					const result = await PostEvaOrderPackageAjax({
						OrderID: this.orderId,
						EvaContent: this.evaContent,
						token: true
					})
					uni.showToast({
						title: result.Result,
						icon: 'none'
					})
				} else {
					const result = await PostEvaOrderHarvestAjax({
						OrderID: this.orderId,
						EvaContent: this.evaContent,
						token: true
					})
					uni.showToast({
						title: result.Result,
						icon: 'none'
					})
				}

				setTimeout(() => {
					uni.navigateBack({
						delta: 1
					})
				}, 800)
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		background: #F7F7F7;
	}

	.boxbg {
		background: #fff;
	}

	.service-number {
		border-bottom: 2rpx solid #efefef;

		.service {
			font-size: 32rpx;
			font-weight: 500;
			color: #333333;
			line-height: 44rpx;
			padding: 30rpx 30rpx 10rpx;
		}

		.order-number {
			font-size: 24rpx;
			font-weight: 400;
			color: #979DA6;
			line-height: 34rpx;
			padding: 0 30rpx 30rpx;
		}
	}

	.textarea {
		margin: 30rpx;
		width: 690rpx;
		height: 240rpx;
		background: #F7F7F7;
		border-radius: 20rpx;
		padding: 30rpx;
		box-sizing: border-box;
	}

	.btn-box {
		padding: 20rpx 30rpx 30rpx;

		.btn {
			text-align: center;
			line-height: 104rpx;
			height: 104rpx;
			background: #00D08F;
			border-radius: 52rpx;
			backdrop-filter: blur(20rpx);
			font-size: 32rpx;
			font-weight: 500;
			color: #FFFFFF;
		}
	}
</style>
